import React from 'react';
import './Mascote.css';
import Puzzflor from '../../assets/puzzflor.jpg';
import Flower from '../../assets/flower.png';
import { useMediaQuery } from '@mui/material';

const Mascote = () => {


    const isSmallScreen = useMediaQuery('(max-width: 900px)');


    return (
        <div className="Mascote">
            <div className='header'>
                <div className='flower-container' style={{  left: isSmallScreen ? "0px" : "90px" }}>
                    <img src={Flower} alt="Flower"  style={{  height: isSmallScreen ? "100px" : "130px" }} />
                </div>
                <h1>Puzzleflor</h1>
            </div>
            <div className='content'>
                <img src={Puzzflor} alt="Puzzflor"  style={{  height: isSmallScreen ? "350px" : "450px" }} />
                <div className='descricao'>
                    <h2>Descrio da Mascote</h2>
                    <p>Puzzleflor  uma flor mgica e brincalhona, cujas ptalas so como peas de puzzle. Cada ptala tem uma cor nica e um padro especial, representando a diversidade e a singularidade de cada pessoa. Ela  conhecida por sua energia contagiante e por encontrar sempre uma maneira divertida de resolver qualquer desafio.</p>
                </div>
                <div className='caracteristicas'>
                    <h2>Caractersticas da Puzzleflor</h2>
                    <ul>
                        <li>Ptalas de Puzzle: Cada ptala  como uma pea de puzzle, variando em cor e padro.</li>
                        <li>Expresso Alegre: Puzzleflor tem olhos brilhantes e um sorriso animado, transmitindo positividade e acolhimento.</li>
                        <li>Personalidade Curiosa: Sempre interessada em conhecer novos amigos e aprender sobre as suas histrias nicas.</li>
                    </ul>
                </div>
                <div className='history'>
                    <h2>Histria da Puzzleflor</h2>
                    <p>
                        Puzzleflor nasceu num jardim encantado onde todas as flores eram especiais. Desde pequena, ela notou que as suas ptalas eram diferentes das outras flores: elas encaixavam-se como um puzzle, formando uma flor nica e colorida. Puzzleflor adorava explorar o jardim e fazer novos amigos entre as flores, animais e at mesmo entre os pequenos seres que viviam ali.
                        Um dia, Puzzleflor decidiu sair numa jornada pelo mundo para descobrir novos lugares e compartilhar a sua mensagem especial. Ao longo das suas aventuras, ela encontrou crianas de diferentes culturas, animais com habilidades nicas e at plantas que tinham histrias incrveis para contar. Em cada lugar que visitava, Puzzleflor espalhava a mensagem de que a diversidade  o que torna o mundo to maravilhoso e que, juntos, somos capazes de criar um lugar melhor para todos.
                    </p>
                </div>
                <div className='message'>
                    <h2>Mensagem da Mascote</h2>
                    <p>"Eu sou Puzzleflor, a flor que mostra como cada um de ns  uma pea nica e importante no grande puzzle da vida. Vamos celebrar a nossa diversidade e aprender uns com os outros, porque juntos somos mais fortes e mais bonitos!"</p>
                </div>
            </div>
        </div>
    );
};

export default Mascote;
